<template>
  <div class="pb-10 mx-2 mt-10 md:w-1/2 md:mx-auto poppins">
    <vee-form
      :validation-schema="schema"
      @submit="register"
      :initial-values="userData"
    >
      <!-- Name -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Name</label>
        <vee-field
          type="text"
          name="name"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Enter Name"
        />
        <ErrorMessage class="text-red-600 dark:text-red-500" name="name" />
      </div>
      <!-- Email -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Email</label>
        <vee-field
          type="email"
          name="email"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Enter Email"
        />
        <ErrorMessage class="text-red-600 dark:text-red-500" name="email" />
      </div>

      <!-- Phone Number -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Phone Number</label>
        <vee-field
          type="tel"
          name="phone"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="e.g. 01612345678"
        />
        <ErrorMessage class="text-red-600 dark:text-red-500" name="phone" />
      </div>

      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Division</label>
        <vee-field
          name="division"
          as="select"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          @change="getDistricts($event.target.value)"
        >
          <option value="" selected disabled>Select Your Division</option>
          <option v-for="(d, i) in divisions" :key="i" :value="d">
            {{ d }}
          </option>
        </vee-field>
        <ErrorMessage class="text-red-600 dark:text-red-500" name="Division" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">District</label>
        <vee-field
          as="select"
          name="district"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          @input="getUpazilas($event.target.value)"
          placeholder="District"
        >
          <option value="" selected disabled>Select Your District</option>
          <option v-for="(d, i) in districts" :key="i" :value="d">
            {{ d }}
          </option>
        </vee-field>
        <ErrorMessage class="text-red-600 dark:text-red-500" name="district" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Upazila</label>
        <vee-field
          as="select"
          name="upazila"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Upazila"
        >
          <option value="" selected disabled>Select Your Upazila</option>
          <option v-for="(d, i) in upazilas" :key="i" :value="d">
            {{ d }}
          </option>
        </vee-field>
        <ErrorMessage class="text-red-600 dark:text-red-500" name="upazila" />
      </div>
      <!-- College -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">College</label>
        <vee-field
          type="college"
          name="college"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="College Name"
        />
        <ErrorMessage class="text-red-600 dark:text-red-500" name="college" />
      </div>
      <div
        :class="reg_alert_variant"
        class="p-5 my-4 font-bold text-center text-white"
        v-if="reg_show_alert"
      >
        {{ reg_alert_msg }}
      </div>
      <button
        type="submit"
        class="
          block
          w-full
          bg-red-500
          text-white
          py-1.5
          px-3
          rounded
          transition
          hover:bg-red-600
        "
        :disabled="reg_in_submission"
      >
        Register
      </button>
    </vee-form>
  </div>
</template>

<script>
import { locations } from "../../plugins/locations";
export default {
  name: "RegisterFrom",
  // beforeRouteEnter(to, from, next) {
  //   let user = localStorage.getItem("s22ssrb");
  //   if (user == undefined && to.query.ttrx) {
  //     next();
  //     return;
  //   }
  //   next({
  //     path: "/restricted",
  //   });
  // },
  data() {
    return {
      schema: {
        name: "required|min:3|max:100",
        email: "required|min:3|max:100|email",
        phone: "required|min:11|max:15",
        college: "required|min:4|max:100",
        division: "required",
        district: "required:min:4",
        upazila: "required|min:2",
      },
      userData: {
        phone: this.$route.query.phone,
      },

      reg_in_submission: false,
      reg_show_alert: false,
      reg_alert_variant: "bg-blue-500",
      reg_alert_msg:
        "Please wait! Untill your registration has been completed.",

      divisions: [],
      districts: [],
      upazilas: [],
    };
  },
  methods: {
    async register(values) {
      this.reg_show_alert = true;
      this.reg_in_submission = true;
      this.reg_alert_variant = "bg-blue-500";
      this.reg_alert_msg =
        "Please wait! Untill your registration has been completed";
      let pl = values.phone.length;
      let phone = values.phone.substring(pl - 10, pl);
      try {
        let body = JSON.stringify({
          ttrx: this.$route.query.ttrx,
          userentry: true,
          userDetails: [
            values.name,
            phone,
            values.email,
            values.college,
            values.division,
            values.district,
            values.upazila,
          ],
        });

        this.$store.dispatch("register", {
          body,
          router: this.$router,
          phone,
        });
      } catch (err) {
        console.log(err);
        this.reg_in_submission = false;
        this.reg_alert_variant = "bg-red-500";
        this.reg_alert_msg =
          "An Unexpected error occurred. Please try again later.";
        return;
      }

      this.reg_alert_variant = "bg-green-500";
      this.reg_alert_msg = "Success! Your account has been registered.";
    },
    getDivisions() {
      this.divisions = [...new Set(locations.map((l) => l.division))];
    },
    getDistricts(c) {
      this.districts = [
        ...new Set(
          locations.filter((l) => l.division == c).map((k) => k.district)
        ),
      ];
      this.upazilas = [];
    },
    getUpazilas(c) {
      this.upazilas = locations
        .filter((l) => l.district == c)
        .map((k) => k.upazila);
    },
  },
  created() {
    this.getDivisions();
  },
};
</script>

<style lang="scss" scoped>
</style>
